<template lang="pug">
  .header
      .menu
        .container
          .menu__wrapper
            .menu__logo: a(href="/"): img(src="/static/logo_color_eng_bb.png")
            .menu__actions.menu__actions--full(v-mousemove-outside="onMouseOutside")
              ul
                li(v-bind:class="{ 'menu-item--active': active == 'about' }") О компании
                li(@click.prevent="visible=true; active='ss'", v-bind:class="{ 'menu-item--active': active == 'ss' }") Услуги
                li(@click.prevent="visible=true; active='prd'", v-bind:class="{ 'menu-item--active': active == 'prd' }") Продукты
                li(@click.prevent="visible=true; active='b2c'", v-bind:class="{ 'menu-item--active': active == 'b2c' }") B2C Софт
              transition(name="slide-fade")
                .menu-dropdown(v-if="visible")
                  .container
                    .menu-dropdown__wrapper
                      .dd__item(v-if="active == 'ss'")
                        .dd__header
                          h2 Information Security Services
                          p Level up your information security using our consulting, audit and development services.
                        .dd__links
                          section
                            header ФЗ-152 "О персональных данных"
                            p Оценка соответствия и приведение в соответствие
                          section
                            header Выполнение требований Банка России
                            p СТО БР ИББС, 552-П, 397-П и пр.
                          section
                            header Экспертные услуги
                            p Разработка документации, аудиты по внутренним требованиям и требованиям партнеров и т.д.
                          section
                            header Консультационная поддержка
                            p Обновление документации, подготовка к проверкам РКН и сопровождение проверок
                          section
                            header Автоматизация процессов ИБ
                            p Разработка технических решений по автоматизации процессов защиты информации
                          section
                            header Поставка и внедрение средств защиты
                            p Поставляем и внедряем средства защиты информации наших партнеров
                      .dd__item(v-if="active == 'prd'")
                        .dd__header
                          h2 Information Security Products
                          p Level up your information security with our tools.
                        .dd__links
                          section
                            header AEGIS | PDM
                            p Управление состоянием защиты ИСПДн, моделирование угроз
                          section
                            header AEGIS | Audit
                            p Проведение внутренних аудитов по вопросам защиты информации
                          section
                            header AEGIS | University
                            p Обучение работников по вопросам защиты информации
                          section
                            header AEGIS | Active Manager
                            p Управление активами, классификация, ведение перечней, матриц доступа и пр.
                          section
                            header AEGIS | Boson
                            p Взаимодействие с работниками по вопросам защиты информации
                          section
                            header AEGIS | BCM
                            p Управление непрерывностью бизнеса и восстановлением после сбоев
                      .dd__item(v-if="active == 'b2c'")
                        .dd__header
                          h2 B2C Security
                          p Продажа клиентам решений по защите информации и другого программного обеспечения
                        .dd__links
                          section
                            header Продажа клиентам программного обеспечения
                            p Узнайте, как можно зарабатывать больше, продавая программное обеспечение
                          section
                            header Chains
                            p Ритейловый шлюз, подклчаясь к которому можно автоматически заказывать программное обеспечение для продажи
                          section
                            header Motiv
                            p Система мотивации работников компании или работников подрядчиков для осуществления дополнительных продаж
            .menu__login
              ul
                li: a(href="#") English
                li: a(href="#") Вход &rarr;
</template>

<script>
export default {
  name: 'aes-header',
  data () {
    return {
      visible: false,
      active: ''
    }
  },
  methods: {
    onMouseOutside (e, el) {
      this.visible = false
      this.active = ''
    }
  }
}
</script>

<style lang="less">
@menu--height: 60px;
@menu--logo--width: 150px;
.menu {
  background-color: #161616;
  color: #fff;
  height: @menu--height;
  &__wrapper {
    display: flex;
    flex-direction: row;
    height: @menu--height;
  }
  &__logo {
    flex: 0 0 @menu--logo--width;
    align-self: center;
  }
  &__actions {
    flex-grow: 1;
    align-self: center;
    -webkit-font-smoothing: antialiased;
    font-weight: 600;
    
    &--full {
      // ul takes full height of actions
      height: 100%;
      display: flex;
      li {
        align-self: center;
        padding: 7px 15px;
        margin-left: 5px !important;
        border-radius: 5px;
        &:hover {
        cursor: pointer;
        background-color: rgba(255, 255, 255, 0.1);
        }
        &.menu-item--active {
          background-color: #fff !important;
          color: #333;
        }
      }
    }
  }

  &__login {
    margin-left: auto;
    align-self: center;
    font-size: 11px;
  }
  ul {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    list-style-type: none;
    li {
      margin-left: 20px;
      a {
        color: #fff;
      }
    }
  }
}

.menu-dropdown {
  z-index: 20000;
  background-color: #fff;
  border-bottom: 1px solid #cccbcb;
  position: absolute;
  left: 0;
  right: 0;
  top: 60px;
}

.dd__item {
  color: #000;
  display: flex;
  flex-direction: row;
  font-weight: normal;
}

.dd__header {
  flex: 0 0 280px;
  background-color: rgba(0,0,0,0.1);
  padding: 50px 20px;
  h2 {
    margin: 0 0 10px 0;
    font-weight: bold;
    font-size: 20px;
  }
}

.dd__links {
  padding: 30px 20px;
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  section {
    flex: 0 1 400px;
    padding: 20px;
    cursor: pointer;
    border: 1px solid transparent;
    border-radius: 5px;
    &:hover {
      border-color: #cccbcb;
    }
    header {
      font-weight: 700;
      font-size: 12px;
      line-height: 14px;
      margin-bottom: 5px;
    }
    p {
      font-size: 12px;
    }
  }
}

/* Enter and leave animations can use different */
/* durations and timing functions.              */
.slide-fade-enter-active {
  transition: all .2s ease;
}
.slide-fade-leave-active {
  transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for <2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
</style>



