<template lang="pug">
  .jumbo(v-bind:class="{ 'jumbo--inverse': inverse }")
    .container
      .jumbo__header.jumbo__header--condensed Feel your information security.
      .jumbo__announcement
        span.new-pill New
        span.message Introducing Aegis University: Tool for security education &amp; awarness
      .jumbo__buttons
</template>

<script>
export default {
  name: 'app',
  props: [
    'inverse'
  ]
}
</script>

<style lang="less">
.jumbo {
  background: #eee;
  height: 800px;
  .container {
    padding: 150px 30px;
  }
  &__header {
    font-size: 80px;
    line-height: 80px;
    font-weight: 700;
    &--condensed {
      width: 700px;
    }
  }
  &__description {
    font-size: 28px;
  }
  &__announcement {
    width: 700px;
    margin-top: 40px;
    line-height: 26px;
    padding: 3px 12px 3px 5px;
    border-radius: 15px;
    color: #fff;
    cursor: pointer;
    // color: #253039;
    text-decoration: none;
    background: rgba(0, 0, 161, .05);
    font-size: 12px;
    z-index: 20;
    .new-pill {
      color: #fff;
      margin-left: 10px;
      padding: 0 6px;
      font-size: 10px;
      line-height: 16px;
      font-weight: 600;
      background: #24b47e;
      text-transform: uppercase;
      border-radius: 10px;
    }

    .message {
      margin: 0 7px 0 6px;
    }

    &:hover {
      text-decoration: none;
      background: rgba(255, 255, 255, .1);
    }
  }

  &--inverse {
    background-color: #161616;
    color: #fff;
  }
}
</style>



